


*{
box-sizing:  0;

}


body {
    margin:0;
    font-family:Karla, sans-seriff;
    

}

.nav{
    display:flex;
    justify-content:center;
    align-items: center;
    color:white;
    gap : 1em;
    background-color:#672280;
    padding: 0.75em 0.625em 0.813em 0.625em;
    flex-wrap: wrap;

}

.nav--image{

  
    width: 4.5em;

}

.nav--title{


  
    font-size: 2em;
    line-height: 1.5em;
letter-spacing: -0.1em;

}

.nav--title_text{
   


}
/* main */
/* main {
    margin :0 20px;
} */

/* form */

.form{
    margin :0 1.25em;

}

.form--inputs{
    display:flex;
    justify-content:center;
    gap:0.75em ;
    margin: 1.563em auto 0.75em auto;
    height:2.5em;

}

.form--input1{
    width:18.75em;
    border-radius: 0.313em;
    border-style:solid;
    border-color: #D5D4D8;

}

.form--input2{
    width:18.75em;
    border-radius: 0.313em;
    border-style:solid;
    border-color: #D5D4D8;
   

}

/* parent */
.form--elements{
    margin:auto;
    width:33.75em;
}

.form--button{
    display:flex;
 
    margin:auto;

    color:white;
    background-color:#672280;
    border:0;
    padding:1.5%;
    width:100%;
    height: 3.125em;
    border-radius: 0.313em;
 

}

/* code below for button animation */

.form--button{
    transition-duration: .1s;
  }
    
  .form--button:active {
    transform: scale(0.9);

}


/* button animation code ends */



.form--button_text{
    display:flex;
    margin:auto;
    font-size: 1.25em;
}



.form--image{
    border-radius: 0.375em;
    margin-top: 1.875em;
    width:100%;
}



.form--image:hover{
    opacity:0.8;
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 0.938em 0;
    padding: 0 0.313em;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 0.063px;
    text-shadow:
        0.125em 0.125em 0 #000,
        -0.125em -0.125em 0 #000,
        0.125em -0.125em 0 #000,
        -0.125em 0.125em 0 #000,
        0 0.125em 0 #000,
        0.125em 0 0 #000,
        0 -0.125em 0 #000,
        -0.125em 0 0 #000,
        0.125em 0.125em 0.313em #000;
}

.text-position{
    position: relative;

}

.bottom {
    position: absolute;
    bottom: 0px;
}

.top {
    position: absolute;

    top: 2.5em;
}